.button {
  border-radius: 12061994px;
}

.button:hover {
  box-shadow: 0 1.5rem 3rem rgba(0, 0, 0, 0.2);
  transform: scale(1.02) translateY(-4px);
}

button:active {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.3);
  transform: scale(0.98) translateY(-2px);
}

.card {
  max-width: 400px;
  min-height: 430px;
  max-height: 430px;
}

.footer {
  background-color: #485461;
  background-image: linear-gradient(315deg, #485461 0%, #28313b 74%);
}

.header-size {
  max-width: 15%;
}

html {
  line-height: 1.5;
  background-color: #485461;
  background-image: linear-gradient(315deg, #485461 0%, #28313b 74%);
  color: white;
}

hr {
  background-color: #b1bfd8;
  border: none;
  content: '';
  height: 1px;
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
  width: 4rem;
}

.footer-list {
  display: flex;
}

.footer-list li {
  list-style-type: none;
  margin: 20px;
}

.profile-img {
  max-width: 46%;
  border-radius: 120694px;
}

.navbar {
  background-color: #b1bfd8;
}

.message-header {
  background-color: #b1bfd8;
}

/* for social links */

.social {
  display: flex;
}

.social li {
  height: 2rem;
  margin-right: 0.5rem;
  text-align: center;
  width: 2rem;
  list-style-type: none;
}

.social a {
  color: white;
  align-items: center;
  display: flex;
  font-size: 1.5rem;
  height: 2rem;
  justify-content: center;
  opacity: 0.5;
  transition-property: opacity, transform;
  width: 2rem;
  will-change: opacity, transform;
}

.social a:hover {
  opacity: 1;
  transform: scale(1.25);
}

.social a:active {
  opacity: 1;
  transform: scale(1.1);
}

.wallpaper {
  background-position: center;
  background-size: cover;
  opacity: 0.2;
  position: fixed;
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
}

@media screen and (max-width: 750px) {
  .switch {
    flex-direction: column-reverse;
    display: flex;
  }
  img {
    max-width: 100%;
  }
  .header-size {
    max-width: 60%;
  }
}
